@import "../../styles/colors";

.current-episode {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
  > * {
    margin-bottom: 40px;
  }

  .current-episode__image {
    width: 180px;
    height: 180px;
    border-radius: 4px;
  }

  .current-episode__title {
    margin-bottom: 16px;
  }

  .current-episode__description{
    color: $light_color;
    pre {
      font-family: 'Source Serif Pro', serif;
    }
  }

  .current-episode__audio {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 39px;
    min-width: 145px;

    .audio-time {
      display: flex;
      align-items: center;
      text-align: justify;
      > h6 {
        color: $color;
        transform: translateY(1px);
        }

    }

    .audio-play-button {
      margin-right: 12px;
      width: 14px;
      height: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: background-color 0.5s ease-in-out;
    }

    .audio-progress-bar{
      margin: 0 8px;
      width: 100%;
      position: relative;

      input[type="range"]{
        width: 100%;
      }
    }

    .audio-volume-button {
      margin-left: 12px;
      cursor: pointer;
    }
    .volume-progress-bar {
        margin: 0 8px;
        width: 100%;
        max-width: 100px;
        position: relative;

        input[type="range"]{
            width: 100%;
        }
    }
    .volume-open {
      width: 100%;
      transition: width 1s;
    }
    .volume-close {
      width: 0;
      transition: width 0.3s;
    }
    .audio-download-button {
      cursor: pointer;
    }

    .audio-button__image {
      width: 16px;
      height: 16px;
    }
  }

  .current-episode__link-title {
    margin-bottom: 12px;
  }
  .current-episode__link {
    width: 130px;
    display: flex;
    flex: 1 0 auto;
    justify-content: space-between;

    > a {
      font-size: 24px;

      color: $dark_color;
      transition: color 0.2s ease-in-out;
      &:hover {
        color: $special_color;
      }
    }
  }
}

.audio-progress-bar__input,
.volume-progress-bar__input {
  appearance: none;
  border-radius: 2px;
  height: 7px;
  cursor: pointer;
  background: $special_background_color;

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    height: 7px;
    border-radius: 2px;
    background: $dark_color;
  }

  &::-webkit-slider-runnable-track {
    height: 7px;
    border-radius: 2px;
    background: $special_background_color;
  }

  &::-webkit-slider-thumb{
    appearance: none;
    border: none;
    height: 0;
    width: 0;
  }

  &::-moz-range-thumb{
    appearance: none;
    border: none;
    height: 0;
    width: 0;
  }

  &::-ms-thumb {
    appearance: none;
    border: none;
    height: 0;
    width: 0;
  }

  &:focus {
    outline: none;
  }

  &:focus::-webkit-slider-runnable-track{
    height: 7px;
    border-radius: 2px;
    background: $special_background_color;
  }
  &:focus::-moz-range-track{
    height: 7px;
    border-radius: 2px;
    background: $special_background_color;
  }
  &:focus::-ms-fill-lower{
    height: 7px;
    border-radius: 2px;
    background: $special_background_color;
  }
  &:focus::-ms-fill-upper {
    height: 7px;
    border-radius: 2px;
    background: $special_background_color;
  }
}

.audio-progress-bar__input::before {
  width: var(--progress-audio-width, 0);
}
.volume-progress-bar__input::before {
  width: var(--progress-volume-width, 0);
}